export default {
  data() {
    return {
      size: 'small',
      imageUrl: '',
      user: sessionStorage.getItem("user"),
      centerDialogVisible: false,//提示页面是否显示
      editDialogVisible: false, // 编辑界面是否显示
      editLoading: false,
      dataFormRules: {
        name: [
          { required: true, message: '请输入名称', trigger: 'blur' }
        ]
      },
      // 新增编辑界面数据
      dataForm: {
        id: 0,
        label: '',
        value: '',
        type: '',
        sort: 0,
        description: '',
        remarks: ''
      }
    };
  },
  methods:{
    toHistory: function() {
      if (this.user!=null){
        this.$router.push({path:'/history'});
      }else{
        this.$confirm("历史搜索记录需要登录才能查询，请登录", "提示", {
          type: "warning"
        })
          .then(() => {
            this.$router.push({path:'/login'});
          })
          .catch(() => {

          })
      }
    },
    toCall(){
      this.$router.push({path:'/call'});
    },
    // 退出登录
    logout: function() {
      this.$confirm("确认退出吗?", "提示", {
        type: "warning"
      })
      .then(() => {
        this.$router.push({path:'/login'})
        sessionStorage.removeItem("user")
        sessionStorage.removeItem("themeColor")
        this.deleteCookie("token")
      })
      .catch(() => {})
    },
    // 删除cookie
    deleteCookie: function(name) {
      Cookies.remove(name)
    },
    updateUser(){
      this.$router.push({path:'/updateUser'})
    },
    // 显示编辑界面
    handleEdit: function () {
      this.editDialogVisible = true
      this.$api.user.findByName({"name":sessionStorage.getItem("user")}).then((res) => {
        if(res.code == 200) {
          this.dataForm = Object.assign({}, res.data)
        } else {
          this.$message({message: '操作失败, ' + res.msg, type: 'error'})
        }
      })
    },
    // 编辑
    submitForm: function () {
      this.$refs.dataForm.validate((valid) => {
        if (valid) {
          this.$confirm('确认提交吗？', '提示', {}).then(() => {
            this.editLoading = true
            let params = Object.assign({}, this.dataForm)
            this.$api.dict.save(params).then((res) => {
              if(res.code == 200) {
                this.$message({ message: '操作成功', type: 'success' })
              } else {
                this.$message({message: '操作失败, ' + res.msg, type: 'error'})
              }
              this.editLoading = false
              this.$refs['dataForm'].resetFields()
              this.editDialogVisible = false
              this.findPage(null)
            })
          })
        }
      })
    },
    // 移除图片
    handleRemove() {
      this.imageUrl = ''
    },
    // 上传成功回调
    handleAvatarSuccess(res, file) {
      this.imageUrl = res.data.url
    },
    // 上传前格式和图片大小限制
    beforeAvatarUpload(file) {
      const type = file.type === 'image/jpeg' || 'image/jpg' || 'image/webp' || 'image/png'
      const isLt2M = file.size / 1024 / 1024 < 2
      if (!type) {
        this.$message.error('图片格式不正确!(只能包含jpg，png，webp，JPEG)')
      }
      if (!isLt2M) {
        this.$message.error('上传图片大小不能超过 2MB!')
      }
      return type && isLt2M
    }
  }
}



