export default {
  data() {
    return {
      isShow:false,
      isDetail:""
    };
  },
  methods: {
    recyclable(){
      this.isDetail=this.$route.params&&this.$route.params.id;
    },
    back(){
      this.$router.go(-1);//返回上一层
    }
  },
  mounted() {
    //一开始会执行
    this.recyclable();
  }
}



